.Subcategorias {
    display: flex;
    flex-direction: column;
    background-color: rgb(241,237,226);
    min-height: 90vh;
    height: auto;
    width: 100%;
}

.head {
    display: flex;
    height: 6vh;
    background-color: rgb(96,91,76);
    margin: 0;
}

.h4Head {
    color: rgb(241,237,226);
    font-family: "Forum", sans-serif;
    font-size: 2vh;
    margin: auto 0 auto 5%;
}

.imgHead {
    height: 80%;
    margin: auto 5% auto auto;
}

.Subcategoria {
    display: flex;
    width: 85%;
    height: 20vh;
    margin: 2vh auto 0 auto;
    cursor: pointer;
}

.movimiento {
    width: 100%;
    object-fit: cover;
    object-position: bottom;
}

.divTitulo {
    display: flex;
    position: absolute;
    width: 85%;
    height: 20vh;
    background-color: rgba(96, 91, 76, 0.552);
}

.titulo {
    margin: auto;
    margin-bottom: 5%;
    color: rgb(241,237,226);
    font-size: 11vw;
    font-weight: normal;
}

.subcategorias {
    width: 85%;
    display: flex;
    margin: 2vh auto;
    flex-wrap: wrap;
    justify-content: space-between;
}

.subcategoria {
    display: flex;
    flex-direction: column;
    width: 45%;
    margin: auto 0;
    text-decoration: none;
    cursor: pointer;
}

.imgSubcategoria {
    width: 100%;
}

.h4Subcategoria {
    color: rgb(96,91,76);
    font-weight: normal;
    margin: 1vh auto 3vh auto;
}