.Perfil {
    display: flex;
    flex-direction: column;
    background-color: rgb(241,237,226);
    min-height: 90vh;
    height: auto;
    width: 100%;
}

.head {
    display: flex;
    height: 6vh;
    background-color: rgb(96,91,76);
    margin: 0;
}

.h4Head {
    color: rgb(241,237,226);
    font-family: "Forum", sans-serif;
    font-size: 2vh;
    margin: auto 0 auto 5%;
}

.imgHead {
    height: 80%;
    margin: auto 5% auto auto;
}

.info {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 35vh;
    margin: 0 auto;
}

.siglas {
    width: 25vw;
    height: 25vw;
    background-color: rgb(55, 55, 55);
    font-weight: normal;
    border-radius: 10vh;
    display: flex;
    margin: auto auto 0 auto;
}

.h1Siglas {
    color: white;
    margin: auto;
    font-weight: normal;
}

.nombre {
    color: rgb(96,91,76);
    font-weight: 600;
    margin:  1vh auto 0 auto;
}

.email {
    color: rgb(96,91,76);
    margin: 1vh auto auto auto;
    font-weight: normal;
}

.Plan {
    display: flex;
    background-color: rgb(96,91,76);
    height: 8vh;
    width: 100%;
}

.planL {
    display: flex;
    flex-direction: column;
    width: 42.5%;
    height: 100%;
    margin: auto 0 auto auto;
}

.subtituloPlan {
    color: rgb(241,237,226);
    margin: auto auto 0 0;
    font-weight: normal;
    font-size: 2vh;
}

.plan {
    color: rgb(241,237,226);
    margin: 0 auto 8% 0;
    font-weight: normal;
    font-size: 2vh;
}

.planR {
    height: 100%;
    width: 45%;
    display: flex;
    margin: auto auto auto 0;
}

.renovacion {
    color: rgb(241,237,226);
    margin: auto auto 8% 0;
    font-weight: normal;
    font-size: 2vh;
}

.menu {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    height: 33vh;
}

.h4Menu {
    margin: auto auto auto 0;
    color: rgb(96,91,76);
    cursor: pointer;
}