.NavBar {
    display: flex;
    height: 10vh;
    width: 100%;
    background-color: rgb(96,91,76);
    position: fixed;
    bottom: 0;
}

.linkIcono {
    width: 25%;
    display: flex;
    flex-direction: column;
    text-decoration: none;
}

.imgIcono {
    width: 30%;
    margin: auto;
    margin-bottom: 0;
}

.imgBlack {
    width: 30%;
    margin: auto;
    margin-bottom: 0;
    filter: invert(1);
}

.h6Icono {
    color: rgb(241, 237, 226);
    margin: auto;
    margin-top: 1vh;
    font-weight: normal;
}
